body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.graph{
  position: fixed;
  width: 100px;
  height: 100px;
  background-color: blue;
  z-index: 100;
}
.Controls{
  position: fixed;
  z-index: 5;
  background-color: whitesmoke;
  bottom: 0;
  width: 100vw;
  height: 100px;
}
.slider{
  width: 700px;
}

.disClock{
  width: 200px;
}

.layerbtn{
  width: 100px;
  height: 30px;
  border-radius: 6px;
  border-width: 0px;
  background-color: #00a4db;
  
  /*border-bottom-color: aquamarine;*/
  margin-right: 1px;
  margin-left: 5px;
  margin-top: 2px;
  font-weight: bold;
  color: white;
}

.selected{
  background-color: #003F87;
}
.Nav{
  z-index: 5;
  position: relative;
  color: white;
  background-color: #003F87;
  width: 200px;  
  padding: 15px;
  border-radius: 10px;
  top: -10px;
  left: -30px;
  
}
.Nav a{
  text-decoration: none;
}

.Nav li{
  list-style-type: none;
  color: white;
}


a:visited { 
  color: white;
}

.Nav .activeRoute{
  font-weight: bolder;
}
