.Nav{
  z-index: 5;
  position: relative;
  color: white;
  background-color: #003F87;
  width: 200px;  
  padding: 15px;
  border-radius: 10px;
  top: -10px;
  left: -30px;
  
}
.Nav a{
  text-decoration: none;
}

.Nav li{
  list-style-type: none;
  color: white;
}


a:visited { 
  color: white;
}

.Nav .activeRoute{
  font-weight: bolder;
}